import Vue from "vue";

const baseFunction = {
  // this.baseFunction.getBaseURL()
  getBaseURL: () => {
    return "http://blueskypay.cn:8090/Home";
  },
  // 字符串转换为数值（可转换为负数）
  divNegNum(val) {
    let newVal = parseFloat(val, 10);
    if (newVal) {
      return newVal;
    } else {
      return 0;
    }
  },
  // 字符串转换为正数
  divNum(val) {
    val = parseFloat(val, 10);
    // 转为正数
    if (val < 0) {
      val = Math.abs(val);
    }

    if (val) {
      return val;
    } else {
      return 0;
    }
  },
  // 数字保留指定小数，返回字符串
  changeNumString(val, decimal) {
    val = this.divNum(val);

    if (val && decimal) {
      val = val.toFixed(decimal);
      return val;
    } else if (val) {
      val = val.toFixed(2);
      return val;
    } else if (decimal) {
      return 0;
    }
  },
  // 数字保留三位小数,小数结尾0不显示
  changeNumFloat(val, decimal) {
    val = parseFloat(val);

    if (val) {
      if (decimal == 2) {
        val = Math.round(val * 100) / 100;
      } else if (decimal == 3) {
        val = Math.round(val * 1000) / 1000;
      }
      return val;
    } else {
      return 0;
    }
  },

  // 存储cookie数据
  saveCookieData(key, val) {
    // 有效天数
    var effectDays = 30;
    var date = new Date();
    date.setTime(date.getTime() + effectDays * 24 * 60 * 60 * 1000);
    var expires = "expires=" + date.toUTCString();
    document.cookie = key + "=" + val + ";" + expires + ";path=/";
    console.log("存储cookie数据成功" + key + ":" + val);
  },

  // 读取cookie数据
  loadCookieData(key) {
    var name = key + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(";");
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        console.log(
          "读取cookie数据成功" + key + ":" + c.substring(name.length, c.length)
        );
        return c.substring(name.length, c.length);
      }
    }
    return "";
  },
  // 存储LocalStorage数据
  saveLocalStorageData(key, val) {
    localStorage[key] = val;
  },

  // 读取LocalStorage数据
  loadLocalStorageData(key) {
    var val = localStorage[key];
    if (val == undefined) {
      val = "";
    }
    return val;
  },
  // 补全位数 根据数值和长度返回字符串
  compleNum(num, length) {
    let str = (num / Math.pow(10, length)).toFixed(length).substr(2);
    return str;
  },
  // 获取当前年 2008
  getDate_nowYear() {
    return new Date().getFullYear();
  },

  // 获取当前月 01
  getDate_nowMonth() {
    return this.compleNum(new Date().getMonth() + 1, 2);
  },

  // 获取当前日 01
  getDate_nowDay() {
    return this.compleNum(new Date().getDate(), 2);
  },

  // 获取当前时 01
  getDate_nowHour() {
    let nowHour = "00" + new Date().getHours();
    return nowHour.substring(nowHour.length - 2, nowHour.length);
  },

  // 获取当前分 01
  getDate_nowMinute() {
    let nowMinute = "00" + new Date().getMinutes();
    return nowMinute.substring(nowMinute.length - 2, nowMinute.length);
  },

  // 获取当前秒 01
  getDate_nowSecond() {
    let nowSecond = "00" + new Date().getSeconds();
    return nowSecond.substring(nowSecond.length - 2, nowSecond.length);
  },
  // 返回当前年月日时分秒  2008-01-01 01:01:01
  getDate_nowYearMonthDayHourMinuteSecond() {
    let nowYear = this.getDate_nowYear();
    let nowMonth = this.getDate_nowMonth();
    let nowDay = this.getDate_nowDay();
    let nowHour = this.getDate_nowHour();
    let nowMinute = this.getDate_nowMinute();
    let nowSecond = this.getDate_nowSecond();
    let nowYearMonthDayHourMinuteSecond =
      nowYear +
      "-" +
      nowMonth +
      "-" +
      nowDay +
      " " +
      nowHour +
      ":" +
      nowMinute +
      ":" +
      nowSecond;
    return nowYearMonthDayHourMinuteSecond;
  },
  // 返回当前年月日 2008-01-01
  getDate_nowYearMonthDay() {
    let nowYear = this.getDate_nowYear();
    let nowMonth = this.getDate_nowMonth();
    let nowDay = this.getDate_nowDay();
    let nowYearMonthDay = nowYear + "-" + nowMonth + "-" + nowDay;
    return nowYearMonthDay;
  },
  // 移动端判断
  mobileAjuge() {
    var ajuge = navigator.userAgent.match(/(Windows)/i);
    // 检测userAgent
    if (ajuge) {
      return false;
    } else {
      console.log("移动端设备");
      return true;
    }
  },
};

// 如果挂载了，在对应vue页面就不需要导入，可直接使用
Vue.prototype.baseFunction = baseFunction;
