<template>
  <div id="index_page" class="" :class="index_page_class">
    <div id="index_header">
      <el-menu
        id="index_header_menu"
        :default-active="navMenu_index"
        class="el-menu-demo"
        mode="horizontal"
        @select="navMenu_change"
      >
        <el-menu-item index="navigation">导航</el-menu-item>
        <el-menu-item index="bookmark">书签</el-menu-item>
      </el-menu>
      <div id="loginOut" @click="loginOut">退出登录</div>
    </div>
    <div id="index_content">
      <router-view @navMenu_index_Change="navMenu_index_Change"></router-view>
    </div>
    <div id="index_bottom"></div>
  </div>
</template>

<script>
import navigation from "@/components/navigation/navigation.vue";
import bookmark from "@/components/bookmark/bookmark.vue";

export default {
  name: "index_page",
  data() {
    return {
      navMenu_index: "navigation",
      index_page_class: "",
      index_header_display: false,
    };
  },
  watch: {
    // "$store.state.pageName": (newPageName) => {
    "$store.state.pageName"(newPageName) {
      this.navMenu_index = newPageName;
    },
  },
  components: { navigation, bookmark },
  methods: {
    navMenu_change(index) {
      console.log("底部标签切换", index);
      this.$router.push({
        name: index,
      });
      this.$store.commit("changepageName", index);
    },
    navMenu_index_Change(index) {
      console.log("index:", index);
      this.navMenu_index = index;
    },
    loginOut() {
      this.baseFunction.saveCookieData("loginKey", "");
      this.$router.push({
        name: "login",
      });
    },
  },
  created() {
    this.navMenu_index = this.$store.state.pageName;

    if (this.$store.state.isMobile) {
      console.log("判断为移动端，修改样式");
      this.index_page_class = "index_mobile_page";
    }
  },
};
</script>
<style lang="less" scoped>
#index_page {
  background-color: #fff;

  #index_header {
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;

    #index_header_menu {
      border: none;
    }

    #loginOut {
      position: absolute;
      right: 25px;
      font-size: 15px;
      color: #828282;
      cursor: pointer;
    }
  }
  #index_header:hover {
    opacity: 1;
    border-bottom: solid 1px #e6e6e6;
  }
}

.index_mobile_page {
  position: fixed !important;
  width: 100% !important;
  height: 100% !important;
  left: 0 !important;
  top: 0 !important;

  #index_header {
    position: relative !important;
    opacity: 1 !important;
    border-bottom: solid 1px #e6e6e6 !important;
    height: 40px;

    #index_header_menu {
      height: 40px;
      .el-menu-item {
        line-height: 40px;
        height: 40px;
      }
    }
  }

  #loginOut {
    font-size: 12px !important;
    right: 10px !important;
  }
}
</style>
